import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  Col,
  Row,
  Form,
  FormGroup,
  Input,
  FormFeedback,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
} from 'reactstrap';

import * as Yup from 'yup';

import logo2 from '../../assets/img/logo2.png';
import Divider from '../../components/Divider';
import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';
import Notification from '../../components/Notification';
import { useAuth } from '../../hooks/auth';
import api from '../../services/api';

const Login = () => {
  const navigate = useNavigate();
  const { signIn } = useAuth();
  const notificationRef = useRef();
  const [validationErrors, setValidationErrors] = useState({});
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [whatsapp, setWhatsapp] = useState('');
  const [password, setPassword] = useState('');
  const [passwordType, setPasswordType] = useState('password');

  const handleSubmit = () => {
    (async () => {
      try {
        const data = {
          name,
          whatsapp,
          email,
          password,
        };

        const schema = Yup.object().shape({
          name: Yup.string().required('Informe seu nome'),
          whatsapp: Yup.string().required('Informe um telefone whatsapp'),
          email: Yup.string()
            .email('E-mail inválido')
            .required('Informe um email'),
          password: Yup.string().required('Informe uma senha'),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        // validation passed
        const res = await api.post('/authenticate', data);
        const { token, user } = res.data;

        notificationRef.current.notify({
          message: 'Cadastro realizado com sucesso, aguarde a aprovação do seu cadastro',
        });

        setTimeout(() => {
          signIn(token, user);
          navigate('/');
        }, 2000);
      } catch (err) {
        console.log(err);
        setValidationErrors({});
        // Validation failed
        if (err instanceof Yup.ValidationError) {
          notificationRef.current.notify({
            message: 'Precisa informar todos os campos para cadastrar-se',
            color: 'warning',
          });
          const ve = {};
          err.inner.forEach((e) => {
            ve[e.path] = e.message;
          });
          setValidationErrors(ve);
          return;
        }

        if (err && err.response && err.response.data) {
          const { message } = err.response.data;

          if (message) {
            notificationRef.current.notify({
              message,
              color: 'danger',
            });

            return;
          }
        }

        notificationRef.current.notify({
          message: 'Falha tentar ao realizar cadastro',
          color: 'danger',
        });
      }
    })();
  };

  return (
    <Container fluid>
      <Notification ref={notificationRef} />
      <Navbar />
      <Row className="justify-content-center justify-content-sm-between px-2 py-3 p-sm-5">
        <Row className="pt-3 justify-content-center justify-content-sm-between">
          <img
            src={logo2}
            alt=""
            style={{
              width: '100%',
              maxWidth: '150px',
              height: '100%',
              maxHeight: '150px',
            }}
          />
          <div className="pt-3 text-center text-sm-left">
            <h5>Baixe E Acesse Pelo Nosso Aplicativo.</h5>
            <h6>Disponível para Android e IOS</h6>
          </div>
        </Row>
        <Col xs="12" sm="8" md="6">
          <Card className="p-0 p-md-3">
            <CardBody>
              <Form>
                <FormGroup>
                  <InputGroup
                    className={`input-group-merge ${
                      validationErrors.name ? 'invalid' : ''
                    }`}
                  >
                    <Input
                      type="name"
                      placeholder="Nome"
                      value={name}
                      invalid={!!validationErrors.name}
                      onChange={({ target: { value } }) => setName(value)}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <i className="fas fa-file-signature" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <FormFeedback>{validationErrors.name}</FormFeedback>
                  </InputGroup>
                </FormGroup>
                <FormGroup>
                  <InputGroup
                    className={`input-group-merge ${
                      validationErrors.whatsapp ? 'invalid' : ''
                    }`}
                  >
                    <Input
                      type="whatsapp"
                      placeholder="Whatsapp"
                      value={whatsapp}
                      invalid={!!validationErrors.whatsapp}
                      onChange={({ target: { value } }) => setWhatsapp(value)}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <i className="fab fa-whatsapp" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <FormFeedback>{validationErrors.whatsapp}</FormFeedback>
                  </InputGroup>
                </FormGroup>
                <Divider />
                <FormGroup>
                  <InputGroup
                    className={`input-group-merge ${
                      validationErrors.email ? 'invalid' : ''
                    }`}
                  >
                    <Input
                      type="email"
                      placeholder="Email"
                      value={email}
                      invalid={!!validationErrors.email}
                      onChange={({ target: { value } }) => setEmail(value)}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <i className="fas fa-user" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <FormFeedback>{validationErrors.email}</FormFeedback>
                  </InputGroup>
                </FormGroup>
                <FormGroup>
                  <InputGroup
                    className={`input-group-merge ${
                      validationErrors.password ? 'invalid' : ''
                    }`}
                  >
                    <Input
                      type={passwordType}
                      placeholder="Senha"
                      value={password}
                      invalid={!!validationErrors.password}
                      onChange={({ target: { value } }) => setPassword(value)}
                    />
                    <InputGroupAddon
                      addonType="append"
                      onClick={() => setPasswordType((prevState) => (prevState === 'password' ? 'text' : 'password'))}
                    >
                      <InputGroupText>
                        <i
                          className={`fas ${
                            passwordType === 'password'
                              ? 'fa-eye-slash'
                              : 'fa-eye'
                          }`}
                        />
                      </InputGroupText>
                    </InputGroupAddon>
                    <FormFeedback>{validationErrors.password}</FormFeedback>
                  </InputGroup>
                </FormGroup>
                <Row>
                  <Col>
                    <Button color="secondary" onClick={handleSubmit} block>
                      Cadastrar-se
                    </Button>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <Button
                      color="link"
                      className="mt-4"
                      onClick={() => navigate('/login')}
                      block
                    >
                      Já tem conta? fazer login.
                    </Button>
                  </Col>
                </Row>
              </Form>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Footer />
    </Container>
  );
};

export default Login;
